.btn-primary,
.btn-primary:hover,
.btn-primary:active,
.btn-primary:visited,
.btn-primary:focus {
    background-color: #4ec281;
    border-color: #4ec281;
}

.btn-danger,
.btn-danger:hover,
.btn-danger:active,
.btn-danger:visited,
.btn-danger:focus {
    background-color: #4ec281;
    border-color: #4ec281;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.75em;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 20;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}


.NavCircleButton {
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.CategoryListNav__list {
  list-style-type: none;
  margin: 25px 0 0;
  padding: 0;
}

.CategoryListNav__button-wrapper {
  display: flex;
}

.CategoryListNav__add-category-button {
  margin: 20px auto;
}
.CategoryListNav__add-category-button:hover {
  background-color: #4ec281;
  color: white;
}

.CategoryListNav__category-link {
  color: white;
  display: block;
  font-family: var(--custom-font);
  margin: 0 0 8px auto;
  max-width: 180px;
  padding: 8px 16px;
  text-decoration: none;
}
.CategoryListNav__category-link:hover {
  color: white;
  cursor: pointer;
}
.CategoryListNav__category-link.active {
  color: #4ec281;
  background-color: white;
}

.CategoryListNav__num-bookmarks {
  color: #adadad;
  display: block;
  float: right;
  font-size: 10px;
}


.Bookmark {
  --bookmark-height: 400px;
  height: var(--bookmark-height);
  width: 320px;
  padding: 10px 0 8px 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Bookmark__title {
  font-size: 0.85rem;
}

.Bookmark__description {
  flex: 1 1;
}

.Bookmark__delete {
  --bookmark-button-height: 32px;
  background-color: #041a3a;
  border: 0;
  color: #bdbcbc;
  float: right;
  font-size: 12px;
  height: var(--bookmark-button-height);
  /* position: relative; */
  top: calc((var(--bookmark-button-height) + 34px) * -1);
  transform-origin: right bottom;
  margin-right: 5px;
  /* transform: rotate(-90deg); */
  /* width: calc(var(--bookmark-height)); */
}

.Bookmark__delete:hover {
  background-color: #bd0909;
  color: white;
  cursor: pointer;
  font-weight: bold;
}

.Bookmark__title a {
  color: slategrey;
  display: block;
  text-decoration: none;
}
.Bookmark__title a:hover {
  color: #4ec281;
}

.Bookmark__footer {
  display: flex;
  width: 300px;
  justify-content: space-between;
}

.Bookmark__footer-buttons {
  /* padding: 10px; */
  display: flex;
  flex: 2 1;
  justify-content: flex-end;
}

.Bookmark__category {
  flex: 1 1;
  font-size: 14px;
  margin: auto;
  /* position: absolute; */
}

.BookmarkList {
  margin-left: 0px;
  margin-right: 0px;
}


.BookmarkPageMain {
  background-color: #efefef;
  height: 100%;
  padding: 10px;
  width: 100%;
}

.BookmarkPageMain__content {
  color: #3a3939;
  max-width: 380px;
}

.Clipit-form {
  background-color: #0b0e13;
  color: #663434;
  padding: 16px;
  max-width: 332px;
}

.CLipit-form label {
  display: block;
  font-size: 16px;
  padding-bottom: 8px;
}

.Clipit-form input,
.Clipit-form textarea,
.Clipit-form select {
  font-size: 14px;
  padding: 8px;
  width: 300px;
}

.Clipit-form select {
  height: 33px;
}

.Clipit-form > .field {
  margin-bottom: 16px;
}
.Clipit-form > .buttons {
  margin-top: 26px;
}

.Clipit-form button {
  background-color: D7E8D4;
  border-radius: 10px;
  border: 0;
  color: #1a0c0c;
  display: block;
  font-size: 18px;
  font-weight: bold;
  padding: 8px;
  text-align: center;
  text-decoration: none;
}
.Clipit-form button[type="submit"]:hover {
  background-color: #29862e;
  color: white;
}




.AddBookmark {
  color: #c63a3a;
}

.AddBookmark h2 {
  margin-left: 16px;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
}

.App__main-container {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
}

.App__nav {
  background: #4ec281;
  display: none;
  min-width: 180px;
  flex-shrink: 1;
  flex-basis: 0%;
}

.App__header {
  display: flex;
  justify-content: space-between;
  background-color: #343a40;
}

.App__header a {
  color: #4ec281;
  font-family: var(--custom-font);
  padding: 8px 16px;
  text-decoration: none;
}

.App__header h1 {
  display: flex;
  text-align: center;
  align-items: center;
  margin-bottom: 0px;
}

.App__main {
  background: #f5f5f5;
  overflow: auto;
  display: flex;
  flex-direction: row;
  flex-grow: 6;
  flex-shrink: 1;
  flex-basis: 0%;
}

